.majorDiv {
  /* border: 5px solid black; */
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding-top: 8%;
}

h1 {
  font-size: 500%;
  text-align: center;
}

button {
  outline: none; /* Gets rid of that default blue highlight border on buttons */
  display: block;
  margin: 0 auto;
  height: 15%;
  width: 25%;
  border-width: 10px;
  font-size: 250%;
  border-radius: 20px;
}

@media (max-width: 576px) {
  button {
    height: 15%;
    width: 90%;
  }
}

@media (min-width: 576.01px) and (max-width: 767.99px) {
  button {
    height: 25%;
    width: 50%;
  }
}

@media (min-width: 768px) and (max-width: 992px) {
  button {
    height: 20%;
    width: 50%;
  }
} 

@media (min-width: 992.01px) and (max-width: 1199.98px) {
  button {
    height: 15%;
    width: 40%;
  }
} 




